import React from 'react'
import pic from "./images/ProfilePicture.jpg";
import resume from "./images/Thanmayee.pdf"
import "./about.css";

const about = () => {
  return (
    <div class=" text-center w row" id='About'>
        <h6>Get to know</h6>
        <h1>About Me</h1>
        <div className='col-6'>
          <img class="about" src={pic} alt="" />
        </div>
        <div class="card-body col-6">
          <div class=" work">
            <div class="col-sm-6">
              <div class="card w-100 ">
                <div class="card-body">
                  <i class='bx bxs-user-badge'></i>
                  <h5 class="card-title">Work Experience</h5>
                  <p class="card-text">1+years</p>
                </div>
              </div>
              <p class="card-text">Ambitious Software engineer proficient in HTML,CSS,javaScript and many more. Passionate about building websites. Open to learn new skills.</p>
              <a href={resume} download="ThanmayeeCV.pdf" class="btn btn-dark">Download CV
              </a>
            </div>
          </div>
        </div>


      </div>

      )
}

      export default about