import React from 'react'
import "./skills.css";

const Skills = () => {
    return (
        <div class=" text-center  skills row" id='Skills' >
            <h1>Skills</h1>
            <h6>My technical level</h6>
            <div class="row sk">
                <div class="col-lg-6 col-sm-6">
                    <div class="card__FB">
                        <div class="card-body cd row">
                            <div class="card-title"><b>Frontend Developer</b></div>
                            <div class="col-6  d-flex flex-column">
                                <i class='bx bxs-badge-check' >HTML</i>
                                <p>Advanced</p>
                                <i class='bx bxs-badge-check' >CSS</i>
                                <p>Basic</p>
                                <i class='bx bxs-badge-check' >JavaScript</i>
                                <p>intermediate</p>
                            </div>
                            <div class="col-6  d-flex flex-column">
                                <i class='bx bxs-badge-check' >Bootstrap</i>
                                <p>Basic</p>
                                <i class='bx bxs-badge-check' >Git</i>
                                <p>intermediate</p>
                                <i class='bx bxs-badge-check' >Reactjs</i>
                                <p>intermediate</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-6 col-sm-6">
                    <div class="card__FB ">
                        <div class="card-body cd row">
                            <div class="card-title"><b>Backend Developer</b></div>
                            <div class="col-6 d-flex flex-column">
                                <i class='bx bxs-badge-check' >Java,SpringBoot</i>
                                <p>intermediate</p>
                                <i class='bx bxs-badge-check' >MySQL</i>
                                <p>Basic</p>
                                <i class='bx bxs-badge-check' >AWS</i>
                                <p>Basic</p>
                            </div>
                            <div class="col-6 d-flex flex-column">
                                <i class='bx bxs-badge-check' >Kafka</i>
                                <p>Basic</p>
                                <i class='bx bxs-badge-check' >OOPS</i>
                                <p>intermediate</p>
                                <i class='bx bxs-badge-check' >Junit</i>
                                <p>Basic</p>
                            </div>
                        </div>
                    </div>

                </div>
            </div>


        </div>

    )
}

export default Skills