import React from 'react'
import "./qualifications.css";
import { useState } from 'react';

const qualifications = () => {
    const [toggleState, setToggleState] = useState(1);

    const toggleTable = (index) => {
        setToggleState(index);
    };
    return (
        <section className="qualifications__section text-center" id='Qualifications'>
            <h1 className="section__title">Qualifications</h1>
            <h6 className="section__subtitle span">My personal journey</h6>
            <div className="qualifications__container container">
                <div className="Edu__Ex">
                    <div className={
                        toggleState === 1 ?
                            'qualifications__button qualifications__active button--flex'
                            : 'qualifications__button button--flex'}
                        onClick={() => toggleTable(1)}
                    >
                        <i className="uil uil-graduation-cap qualification__icon qualification__active"></i>
                        {"  "}Education
                    </div>
                    <div className={
                        toggleState === 2 ?
                            'qualifications__button qualifications__active button--flex'
                            : 'qualifications__button button--flex'}
                        onClick={() => toggleTable(2)}
                    >
                        <i className="uil uil-briefcase-alt qualification__icon"></i>
                        {"  "}Experience
                    </div>
                </div>
                <div className="qualification__section">
                    <div className={
                        toggleState === 1 ?
                            'qualification__content qualification__content-active'
                            : 'qualification__content'}>
                        <div className="qualification__data">
                            <h5 className="qualification__title">MVJ</h5>
                            <span className="qualification__subtitle">BE - Medical Electronics</span>
                            <div className="qualification__calendar">
                                <i className="uil uil-calendar-alt">2017-2021</i>
                                <div className="qualification__grade">70%</div>
                            </div>
                        </div>
                        <div>
                            <span className="qualification__rounder"></span>
                            <span className="qualification__line"></span>
                        </div>
                        <div className="qualification__data">
                            <h5 className="qualification__title">JNC</h5>
                            <span className="qualification__subtitle">PCMB</span>
                            <div className="qualification__calendar">
                                <i className="uil uil-calendar-alt">2015-2017</i>
                                <div className="qualification__grade">86%</div>
                            </div>
                        </div>
                        <div>
                            <span className="qualification__rounder"></span>
                            <span className="qualification__line"></span>
                        </div>
                        <div className="qualification__data">
                            <h5 className="qualification__title">KGS</h5>
                            <div className="qualification__calendar">
                                <i className="uil uil-calendar-alt">2009 - 2015</i>
                                <div className="qualification__grade">93.92%</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="qualification__section">
                    <div className={
                        toggleState === 2 ?
                            'qualification__content qualification__content-active'
                            : 'qualification__content'}>
                        <div className="qualification__data">
                            <h5 className="qualification__title">TCS</h5>
                            <span className="qualification__subtitle">ASE-trainee</span>
                            <div className="qualification__calendar">
                                <i className="uil uil-calendar-alt">Feb 2022 - present</i>
                            </div>
                        </div>
                        <div>
                            <span className="qualification__rounder"></span>
                            <span className="qualification__line"></span>
                        </div>
                        <div className="qualification__data">
                            <h5 className="qualification__title">Electrono Solutions</h5>
                            <span className="qualification__subtitle">Fronend Developer</span>
                            <div className="qualification__calendar">
                                <i className="uil uil-calendar-alt">Sep 2021 - Jan 2022</i>
                            </div>
                        </div>
                        <div>
                            <span className="qualification__rounder"></span>
                            <span className="qualification__line"></span>
                        </div>
                        <div className="qualification__data">
                            <h5 className="qualification__title">AlgoShack</h5>
                            <span className="qualification__subtitle">Test Automation Engineer</span>
                            <div className="qualification__calendar">
                                <i className="uil uil-calendar-alt">Mar 2021 - Jun 2021</i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section >

    )
}

export default qualifications